import React, { useEffect, useMemo, useState } from 'react';
import { useParams } from "react-router-dom";
import { Link, useHistory } from 'react-router-dom';
import { PadWalletAddress } from 'utils/padwallet';
import { useWallet } from '@binance-chain/bsc-use-wallet';
import { useBep20Contract, useKaiECOContract, useKaiECOERC721Contract, useKaiECOMarketplaceContract, useNFTMarketplaceContract } from 'hooks/useContract';
import { getKaiECOMarketplaceAddress } from 'utils/addressHelpers';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { RootStateOrAny, useDispatch, useSelector } from 'react-redux';
import { actionConstants } from 'constants/actionConstants';
import useWeb3 from 'hooks/useWeb3';
import contracts from 'constants/contracts';
import { getBalanceNumber, getDecimalAmount } from 'utils/formatBalance';
import { getMintContract } from 'utils/contractHelpers';
import { marketplaceService } from 'services';
import CountDown from 'components/CountDown/CountDown';
import { tokens } from 'constants/tokens';
import Bep20ABI from 'constants/abi/Bep20.json';
import { AbiItem } from 'web3-utils'
import ModalBuyNow from 'components/Modal/ModalBuyNow';
import { symbol } from 'utils/symbol';
import { decimal } from 'utils/decimal';
import BigNumber from 'bignumber.js';





const ItemDetails = () => {
    const dispatch = useDispatch();
    const { account } = useWallet();
    const { collection, tokenId } = useParams();
    const [tokenData, setTokenData] = useState(null);
    const [saleFlag, setSaleFlag] = useState(false);
    const [bidFlag, setBidFlag] = useState(false);
    const [buyFlag, setBuyFlag] = useState(false);
    const [removeFlag, setRemoveFlag] = useState(false);

    const web3 = useWeb3();
    const [price, setPrice] = useState(0);
    const [bidPrice, setBidPrice] = useState(0);
    const kaiECOMarketplaceAddress = getKaiECOMarketplaceAddress();
    const marketplaceContract = useKaiECOMarketplaceContract(kaiECOMarketplaceAddress);
    const [histories, setHistories] = useState([]);

    const [nftContract, setNFTContract] = useState(null);
    const KaiECO721Contract = useKaiECOERC721Contract(collection);
    const [token, setToken] = useState(tokens.Nutgain.address);
    useEffect(() => {
        const loadData = async () => {
            let item = await marketplaceContract.methods.viewItemByCollectionAndTokenId(collection, tokenId).call()
            let token = await KaiECO721Contract.methods.getTokenData(tokenId).call();
            let asset = {
                id: token.id,
                status: item.status,
                price: item.itemInfo.price,
                seller: item.itemInfo.seller,
                contract: item.itemInfo.coin,
                uri: `https://ipfs.io/ipfs/${token.uri}`,
                collection: collection,
                name: token.name,
                description: token.description
            }
            setTokenData(asset);
        }
        loadData()

    }, [])

    useEffect(() => {


        async function loadHistory() {

            let assets = [];
            if (tokenData != null) {

                let items = await marketplaceService.getHistory(collection, tokenData.id);

                items.map(bid => {
                    let item = {
                        "id": bid.id,
                        "tokenId": bid.tokenId,
                        "collection": bid.collection,
                        "content": bid.content,
                        "createTime": bid.created_at
                    }
                    assets.push(item);
                });
                console.log(assets)
                setHistories(assets);
            }


        }

        const interval = setInterval(() => {
            loadHistory();
          }, 20000);
          return () => clearInterval(interval);

        //loadBid();
        
        if (tokenData != null) {
            console.log(tokenData);
            setNFTContract(getMintContract(tokenData?.contract, web3));
            if (tokenData.seller != account) {
                if (tokenData.status === true) {
                    setBidFlag(false);
                    setRemoveFlag(false);
                    setSaleFlag(false);
                    setBuyFlag(true);

                } else {
                    setBidFlag(true);
                    setRemoveFlag(false);
                    setSaleFlag(false);
                    setBuyFlag(false);
                }
            } else {
                if (tokenData.status === true) {
                    setBidFlag(false);
                    setRemoveFlag(true);
                    setSaleFlag(false);
                    setBuyFlag(false);
                } else {
                    setBidFlag(false);
                    setRemoveFlag(false);
                    setSaleFlag(true);
                    setBuyFlag(false);
                }
            }
        }
    }, [tokenData])


    const RemoveSale = () => {
        let itemId = tokenData?.id;
        if (itemId == null) return;
        console.log(marketplaceContract);
        marketplaceContract.methods.cancelItemOrder(collection, itemId).send({ from: account })
            .on('transactionHash', (hash) => {
                toast.success(hash, {
                    position: "top-right",
                    autoClose: 3000,
                    theme: 'dark',
                    pauseOnHover: false
                });
            })
            .on('error', (error) => {
                toast.info(error, {
                    position: "top-right",
                    autoClose: 3000,
                    theme: 'dark',
                    pauseOnHover: false
                });
            })
            .on('receipt', async (receipt) => {


                await marketplaceService.putHistory(collection, tokenData.id, `remove by ${PadWalletAddress(account)}`)
                
                setBidFlag(false);
                setRemoveFlag(false);
                setSaleFlag(true);
                setBuyFlag(false);
            })
        // KaiECO721Contract.methods.approve(kaiECOMarketplaceAddress, tokenId).send({ from: account })
        //     .on('receipt', function (receipt) {

        //     })

    }
    const PutOnSale = async () => {
        let itemId = tokenData?.id;
        console.log(price, tokenData.contract)
        //const tokenPrice = web3.utils.toWei(price.toString(), 'gwei');
        dispatch({ type: 'CHANGE_LOAD', payload: true })
        try {

            let decimals = decimal(token);
            let tokenPrice = getDecimalAmount(new BigNumber(price), decimals);
            await KaiECO721Contract.methods.approve(kaiECOMarketplaceAddress, tokenId).send({ from: account })
            await marketplaceContract.methods.modifyItemOrder(collection, itemId, tokenPrice).send({ from: account })
                .on('error', (error) => {
                    toast.error(error, {
                        position: "top-right",
                        autoClose: 3000,
                        theme: 'dark',
                        pauseOnHover: false
                    });
                    dispatch({ type: 'CHANGE_LOAD', payload: false })
                })
                .on('transactionHash', (hash) => {

                    toast.success(hash, {
                        position: "top-right",
                        autoClose: 3000,
                        theme: 'dark',
                        pauseOnHover: false
                    });
                })
                .on('receipt', (result) => {
                    console.log(result)
                    tokenData.status = true;
                    tokenData.price = price;
                    //dispatch({ type: actionConstants.UPDATE_ITEM, payload: tokenData })
                    marketplaceService.putHistory(collection, tokenData.id, `put on sale with ${price}kaiECO by ${PadWalletAddress(account)}`)
                    setBidFlag(false);
                    setRemoveFlag(true);
                    setSaleFlag(false);
                    setBuyFlag(false);
                    dispatch({ type: 'CHANGE_LOAD', payload: false })
                })
        } catch (e) {
            console.log(e)
            dispatch({ type: 'CHANGE_LOAD', payload: false })
        }


    }

    const BuyNow = async () => {
        const itemId = tokenData?.itemId;
        dispatch({ type: 'CHANGE_LOAD', payload: true })
        try {
            const contract = useBep20Contract(token);
            await contract.methods.approve(kaiECOMarketplaceAddress, tokenData?.price).send({ from: account });
            marketplaceContract.methods.BuyItem(collection, itemId).send({ from: account })
                .on('error', (error) => {
                    toast.error(error, {
                        position: "top-right",
                        autoClose: 3000,
                        theme: 'dark',
                        pauseOnHover: false
                    });
                    dispatch({ type: 'CHANGE_LOAD', payload: false })
                })
                .on('transactionHash', (hash) => {

                    toast.success(hash, {
                        position: "top-right",
                        autoClose: 3000,
                        theme: 'dark',
                        pauseOnHover: false
                    });
                })
                .on('receipt', async (receipt) => {
                    tokenData.creator = account;
                    dispatch({ type: actionConstants.UPDATE_ITEM, payload: tokenData })
                    await marketplaceService.putSeller(tokenData.creator, price)
                    //await marketplaceService.putHistory(tokenData.itemId, `Buy with ${price}kaiECO by ${PadWalletAddress(account)}`)
                    dispatch({ type: 'CHANGE_LOAD', payload: false })
                })
        } catch (e) {
            console.log(e);
            dispatch({ type: 'CHANGE_LOAD', payload: false })
        }



    }

    const PlaceOnBid = async () => {
        const collection = tokenData?.contract;
        const itemId = tokenData?.itemId;
        const createTime = new Date().getTime() / 1000;
        const contract = new web3.eth.Contract((Bep20ABI as unknown) as AbiItem, tokenData?.token);


        dispatch({ type: 'CHANGE_LOAD', payload: true })
        try {
            await contract.methods.approve(kaiECOMarketplaceAddress, bidPrice).send({ from: account })
            marketplaceContract.methods.placeOnBid(collection, token, itemId, bidPrice, createTime.toFixed()).send({ from: account })
                .on('error', (error) => {
                    toast.error(error, {
                        position: "top-right",
                        autoClose: 3000,
                        theme: 'dark',
                        pauseOnHover: false
                    });
                    dispatch({ type: 'CHANGE_LOAD', payload: false })
                })
                .on('transactionHash', (hash) => {

                    toast.success(hash, {
                        position: "top-right",
                        autoClose: 3000,
                        theme: 'dark',
                        pauseOnHover: false
                    });

                })
                .on('receipt', async (receipt) => {
                    //await marketplaceService.putHistory(tokenData.itemId, `place on bid with ${bidPrice}kaiECO by ${PadWalletAddress(account)}`)
                    dispatch({ type: 'CHANGE_LOAD', payload: false })
                })
        } catch (e) {
            console.log(e)
            dispatch({ type: 'CHANGE_LOAD', payload: false })
        }




    }

    const Accept = (id, owner, price) => {

        const contract = tokenData?.contract;
        const itemId = tokenData?.itemId;
        //await kaiECOContract.methods.approve(owner, price).call({ from: account });
        // nftContract.methods.approve(kaiECOMarketplaceAddress, tokenData.tokenId).send({ from: account })
        //     .on('receipt', function (receipt) {

        //     });
        dispatch({ type: 'CHANGE_LOAD', payload: true })
        try {
            let createTime = (new Date().getTime() / 1000).toFixed();
            marketplaceContract.methods.acceptBidItem(contract, itemId, id, createTime).send({ from: account })
                .on('receipt', async function (receipt) {

                    let tokenPrice = getBalanceNumber(tokenData.price, decimal(tokenData.token));
                    await marketplaceService.putSeller(tokenData.creator, tokenData.price);
                    //await marketplaceService.putHistory(tokenData.itemId, `accept with ${tokenPrice} ${symbol(tokenData.token)} by ${PadWalletAddress(account)}`)
                    dispatch({ type: 'CHANGE_LOAD', payload: false })
                })
                .on('error', function (error) {
                    toast.error(error, {
                        position: "top-right",
                        autoClose: 3000,
                        theme: 'dark',
                        pauseOnHover: false
                    });
                    dispatch({ type: 'CHANGE_LOAD', payload: false })
                })
        } catch (e) {
            console.log(e)
            dispatch({ type: 'CHANGE_LOAD', payload: false })
        }

    }

    return (
        <section className="item-details-area">
            <div id="putOnSale" className="modal fade p-0" >
                <div className="modal-dialog dialog-animated">
                    <div className="modal-content h-100">
                        <div className="modal-header" data-dismiss="modal">
                            Put On Sale <i className="far fa-times-circle icon-close" />
                        </div>
                        <div className="menu modal-body">
                            <div className="row align-self-center">

                                <div className="col-8">
                                    <div className="form-group">
                                        <input type="number" className="form-control" name="name" placeholder="Item Price" required={true} onChange={e => setPrice(parseFloat(e.target.value))} />

                                    </div>
                                </div>
                                <div className="col-4">
                                    <div className="form-group">

                                        <label className='form-control'>{symbol(tokenData?.contract)}</label>
                                    </div>
                                </div>
                                <div className="col-12">
                                    <button className="btn w-100 mt-3 mt-sm-4" type="button" onClick={PutOnSale} >Put On Sale</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            {
                tokenData && <ModalBuyNow data={tokenData} collection={collection} />
            }
            {/* {
                tokenData && <ModalBuyNow data= { tokenData} collection = {collection} />
            }
            {
                tokenData &&  <ModalBid data = {tokenData} collection = {collection}/>
            } */}


            <div className="container">
                <div className="row justify-content-between">
                    <div className="col-12 col-lg-5">
                        <div className="item-info">
                            <div className="item-thumb text-center">
                                <img src={tokenData?.uri} alt="" />
                            </div>
                            {/* <CountDown auctionTime = {tokenData?.auctionTime} />
                             */}
                            {/* Netstorm Tab */}
                            <ul className="netstorm-tab nav nav-tabs" id="nav-tab">

                                <li>
                                    <a className='active' id="nav-profile-tab" data-toggle="pill" href="#nav-profile">
                                        <h5 className="m-0">{'History'}</h5>
                                    </a>
                                </li>
                                <li>
                                    <a id="nav-contact-tab" data-toggle="pill" href="#nav-contact">
                                        <h5 className="m-0">{'Detail'}</h5>
                                    </a>
                                </li>
                            </ul>
                            {/* Tab Content */}
                            <div className="tab-content" id="nav-tabContent">

                                <div className="tab-pane fade active show" id="nav-profile">
                                    <ul className="list-unstyled">
                                        {/* Single Tab List */}
                                        {histories?.map((item, idx) => {
                                            return (
                                                <li key={`tdt_${idx}`} className="single-tab-list d-flex align-items-center">
                                                    <img className="avatar-sm rounded-circle mr-3" src={"/img/user.jpg"} alt="" />
                                                    <p className="m-0">{item.content}<br />{item.createTime}</p>
                                                </li>
                                            );
                                        })}
                                    </ul>
                                </div>
                                <div className="tab-pane fade" id="nav-contact">
                                    {/* Single Tab List */}
                                    <div className="owner-meta d-flex align-items-center mt-3">
                                        <span>Owner</span>
                                        <Link className="owner d-flex align-items-center ml-2" to="/author">
                                            <img className="avatar-sm rounded-circle" src={'/img/user.jpg'} alt="" />
                                            <h6 className="ml-2">{tokenData && PadWalletAddress(tokenData?.seller)}</h6>
                                        </Link>
                                    </div>

                                </div>
                            </div>
                        </div>
                    </div>


                    <div className="col-12 col-lg-6">
                        {/* Content */}
                        <div className="content mt-5 mt-lg-0">
                            <h3 className="m-0">{tokenData?.name}</h3>
                            <p>{tokenData?.description}</p>
                            {/* Owner */}
                            <div className="owner d-flex align-items-center">
                                <span>Owned By</span>
                                <a className="owner-meta d-flex align-items-center ml-3" href="/author">
                                    <img className="avatar-sm rounded-circle" src={'/img/user.jpg'} alt="" />
                                    <h6 className="ml-2">{tokenData && PadWalletAddress(tokenData?.seller)}</h6>
                                </a>
                            </div>
                            {/* Item Info List */}
                            <div className="item-info-list mt-4">
                                <ul className="list-unstyled">
                                    <li className="price d-flex justify-content-between">
                                        <span>Current Price {tokenData && getBalanceNumber(tokenData?.price, decimal(tokenData?.contract))} {tokenData && symbol(tokenData?.contract)}</span>
                                    </li>

                                </ul>
                            </div>
                            <div className="row items">
                                <div className="col-12 col-md-6 item px-lg-2">
                                    <div className="card no-hover">
                                        <div className="single-seller d-flex align-items-center">

                                            {/* Seller Info */}
                                            <div className="seller-info ml-3">
                                                <Link className="seller mb-2" to={`/user/${tokenData?.seller}`}>{tokenData && PadWalletAddress(tokenData?.seller)}</Link>
                                                <span>Seller</span>
                                            </div>
                                        </div>
                                    </div>

                                </div>
                                <div className="col-12 col-md-6 item px-lg-2">
                                    <div className="card no-hover">
                                        <div className="single-seller d-flex align-items-center">
                                            {/* Seller Info */}
                                            <div className="seller-info ml-3">
                                                <Link className="seller mb-2" to={`/collection/${tokenData?.contract}`}>{tokenData && PadWalletAddress(tokenData?.contract)}</Link>
                                                <span>Collection</span>
                                            </div>
                                        </div>
                                    </div>

                                </div>
                                {/* <div className="col-12 item px-lg-2">
                                    <div className="card no-hover">
                                        <h4 className="mt-0 mb-2">Highest Bid</h4>
                                        <div className="price d-flex justify-content-between align-items-center">
                                            <span>{bids?.sort((pre, next) => next.price - pre.price)[0]?.price} {symbol(token)}</span>
                                            <span>{bids?.length}</span>
                                        </div>
                                    </div>
                                </div> */}
                            </div>
                            <div className='row'>
                                {
                                    removeFlag &&
                                    <>
                                        <div className='col-12'>
                                            <Link className="d-block btn btn-bordered-white mt-4" to="#" onClick={() => RemoveSale()}>{'Remove from Sale'}</Link>

                                        </div>

                                    </>

                                }
                                {
                                    saleFlag &&
                                    <>
                                        <div className='col-12'>
                                            <a className="d-block btn btn-bordered-white mt-4" href="/#" data-toggle="modal" data-target="#putOnSale">{'Put on Sale'}</a>

                                        </div>

                                    </>
                                }
                                {
                                    bidFlag &&
                                    <>
                                        <div className='col-6'>
                                            <a className="d-block btn btn-bordered-white mt-4" href="/#" data-toggle="modal" data-target="#PlaceOnBid">{'Transfer'}</a>

                                        </div>
                                        <div className='col-6'>
                                            <a className="d-block btn btn-bordered-white mt-4" href="/wallet-connect">{'Share'}</a>

                                        </div>
                                    </>
                                }
                                {
                                    buyFlag &&
                                    <>
                                        <div className='col-12'>
                                            <a className="d-block btn btn-bordered-white mt-4" href="/#" data-toggle="modal" data-target="#BuyNow">{'Buy Now'}</a>

                                        </div>

                                    </>
                                }
                            </div>

                        </div>
                    </div>
                </div>
            </div>
            <ToastContainer />
        </section>
    );
}

export default ItemDetails;