import React, { useEffect, useState } from 'react';
import { useBep20Contract, useKaiCollectionContract, useKaiECOMarketplaceContract } from 'hooks/useContract';
import { useWallet } from '@binance-chain/bsc-use-wallet';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

import { connect, useDispatch } from 'react-redux';
import useWeb3 from 'hooks/useWeb3';
import { getKaiECOMarketplaceAddress } from 'utils/addressHelpers';
import { marketplaceService } from 'services';
import Bep20ABI from 'constants/abi/Bep20.json';
import { AbiItem } from 'web3-utils' 
import { PadWalletAddress } from 'utils/padwallet';
import { getBalanceNumber } from 'utils/formatBalance';
import { useBNBBalance, useTokenBalance } from 'hooks/useTokenBalance';
import { actionConstants } from 'constants/actionConstants';
import { symbol } from 'utils/symbol';
import { decimal } from 'utils/decimal';
import { tokens } from 'constants/tokens';
import BigNumber from 'bignumber.js';
import { BIG_ZERO } from 'utils/bigNumber';

function ModalBuyNow(props) {
    const dispatch = useDispatch();
    const { account } = useWallet();
    const tokenData = props.data;
    const collection = props.collection;
    const web3 = useWeb3();
    const [bidPrice, setBidPrice] = useState(0);
    const kaiECOMarketplaceAddress = getKaiECOMarketplaceAddress();
    const marketplaceContract = useKaiECOMarketplaceContract(kaiECOMarketplaceAddress);
    const [balance, setBalance] = useState(BIG_ZERO);
    const bnb_balance = useBNBBalance();
    const token_balance = useTokenBalance(tokenData?.contract)
    useEffect(() => {
        if(tokenData?.contract.toLowerCase() === tokens.bnb.address.toLowerCase()){
            setBalance(bnb_balance);
            
        }else{
            setBalance(token_balance);
        }
        
    })
    const contract = useBep20Contract(tokenData?.contract);
    const BuyNow = async () => {
        if(tokenData?.price > balance){
            toast.error('Balance is small than the Price!', {
                position: "top-right",
                autoClose: 3000,
                theme: 'dark',
                pauseOnHover: false
            });
                
        }
        const itemId = tokenData?.id;
        dispatch({ type: 'CHANGE_LOAD', payload: true })
        try{
            
            
            if(tokenData?.contract.toLowerCase() === tokens.bnb.address.toLowerCase()){
                await marketplaceContract.methods.buyTokenUsingBNB(collection, itemId).send({from : account, value: tokenData.price})
                .on('error', (error) => {
                    toast.error(error, {
                        position: "top-right",
                        autoClose: 3000,
                        theme: 'dark',
                        pauseOnHover: false
                    });
                    dispatch({ type: 'CHANGE_LOAD', payload: false })
                })
                .on('transactionHash', (hash) => {

                    toast.success(hash, {
                        position: "top-right",
                        autoClose: 3000,
                        theme: 'dark',
                        pauseOnHover: false
                    });
                })
                .on('receipt', async (receipt) => {
                    tokenData.seller = account;
                    dispatch({ type: actionConstants.UPDATE_ITEM, payload: tokenData})
                    //await marketplaceService.putSeller(tokenData?.owner, tokenData?.price)
                    //await marketplaceService.putHistory(tokenData?.itemId, `Buy with ${tokenData?.price} ${symbol(tokenData?.token)} by ${PadWalletAddress(account)}`)
                    dispatch({ type: 'CHANGE_LOAD', payload: false })
                })
            }else{
                console.log(tokenData.price);
                await contract.methods.approve(kaiECOMarketplaceAddress, tokenData?.price).send({from : account});
                await marketplaceContract.methods.buyTokenUsingCoin(collection,tokenData.contract, itemId, tokenData?.price).send({ from: account })
                    .on('error', (error) => {
                        toast.error(error, {
                            position: "top-right",
                            autoClose: 3000,
                            theme: 'dark',
                            pauseOnHover: false
                        });
                        dispatch({ type: 'CHANGE_LOAD', payload: false })
                    })
                    .on('transactionHash', (hash) => {

                        toast.success(hash, {
                            position: "top-right",
                            autoClose: 3000,
                            theme: 'dark',
                            pauseOnHover: false
                        });
                    })
                    .on('receipt', async (receipt) => {
                        tokenData.seller = account;
                        dispatch({ type: actionConstants.UPDATE_ITEM, payload: tokenData})
                        await marketplaceService.putSeller(tokenData?.creator, tokenData?.price)
                        await marketplaceService.putHistory(collection, tokenData?.id, `Buy with ${tokenData?.price} ${symbol(tokenData?.token)} by ${PadWalletAddress(account)}`)
                        dispatch({ type: 'CHANGE_LOAD', payload: false })
                    })
            }
            
        }catch(e){
            console.log(e);
            dispatch({ type: 'CHANGE_LOAD', payload: false })
        }
        


    }

    return (
        <div id="BuyNow" className="modal fade p-0" >
            <div className="modal-dialog dialog-animated">
                <div className="modal-content h-100">
                    <div className="modal-header" data-dismiss="modal">
                        Buy Item <i className="far fa-times-circle icon-close" />
                    </div>
                    <div className="menu modal-body">
                        <div className="row align-self-center">

                            <div className="col-12">
                                <div className="form-group">
                                    <span style={{ fontSize: '18px', fontWeight: 900, fontFamily: 'inherit' }} className=" mt-0 mb-3">Address : </span>
                                    <span style={{ fontSize: '18px', fontWeight: 900, fontFamily: 'inherit' }} className=" mt-0 mb-3">{account && PadWalletAddress(account)}</span>
                                </div>
                            </div>
                            <div className="col-12">
                                <div className="form-group">
                                    <span style={{ fontSize: '18px', fontWeight: 900, fontFamily: 'inherit' }} className=" mt-0 mb-3">Balalnce : </span>
                                    <span style={{ fontSize: '18px', fontWeight: 900, fontFamily: 'inherit' }} className=" mt-0 mb-3">{getBalanceNumber(balance, decimal(tokenData.contract))} {symbol(tokenData.contract)}</span>
                                </div>
                            </div>
                            <div className="col-12">
                                <div className="form-group">
                                    <span style={{ fontSize: '18px', fontWeight: 900, fontFamily: 'inherit' }} className=" mt-0 mb-3">You Will Pay : </span>
                                    <span style={{ fontSize: '18px', fontWeight: 900, fontFamily: 'inherit' }} className=" mt-0 mb-3">{tokenData && getBalanceNumber(tokenData.price, decimal(tokenData.contract)) } {tokenData && symbol(tokenData.contract)}</span>
                                </div>
                            </div>
                            <div className="col-12">
                                <button className="btn w-100 mt-3 mt-sm-4" type="button" onClick={BuyNow} >Buy Now</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
export default ModalBuyNow
